<template>
<div class="game">
   <section class="gameInfoWrapper">
      <div class="row">
         <div class="col">
            <div class="label"><h3>SCORE</h3></div>
            <div class="value"><p>{{this.getScore}}</p></div>
         </div>
         <div class="col">
            <div class="labelInfo"><span class="infoPauce">USE SPASE TO PAUSE</span></div>
         </div>
         <div class="col">
            <div class="label"><h3>SPEED</h3></div>
            <div class="value"><p>{{this.getNameSpeed}}</p></div>
         </div>
      </div>
   </section>
   <section class="gameArea">
      <div class="divImageForMap">
         <img src="../assets/image/template-logo-icon-face-head-cobra-snake-black-color-free-vector.jpg" class="imgPages">
      </div>
      <div class="wrapperMap">
         <div class="map">
            <div class="line"
            v-for="line in mapState" 
            :key="line" >
               <div class="block" 
               :class="blockColor(block)"
               v-for="block in line"
               :block="block" 
               :key="block">
               </div>
            </div>
         </div>
      </div>
      <div class="divImageForMap">
         <img src="../assets/image/template-logo-icon-face-head-cobra-snake-black-color-free-vector.jpg" class="imgPages">
      </div>
   </section>
</div>
</template>

<script>
import * as constants from '../constants/constants.js'
import useSound from 'vue-use-sound'
import sfx from '../assets/music/Food.mp3'

export default {
   name: "GameVue",
   created() {
      this.startGame()
      this.click()
   },
   computed: {
        mapState() {
            return this.$store.getters['mapState']
        },
        snake() {
            return this.$store.getters['getSnake']
        },
        getFood() {
            return this.$store.getters['getFood']
        },
        snakeDirection() {
            return this.$store.getters['getSnakeDirection']
        },
        getSnakeHead() {
            return this.$store.getters['getSnakeHead']
        },
        getNextSnakeHead() {
            return this.$store.getters['getNextSnakeHead']
        },
        getNewSnakeDirection() {
            return this.$store.getters['getNewSnakeDirection']
        },
        gameOver() {
            return this.$store.getters['gameOver']
        },
        getScore() {
         return this.$store.getters['getScore']
        },
        setup() {
            const [play] = useSound(sfx)
            return {
               play,
            }
         },
        getSpeed() {
         return this.$store.getters['getSpeed']
        },
        getNameSpeed() {
         return this.$store.getters['getNameSpeed']
        },
    },
    watch:{
      score() {
         this.play({id:"destroy"})
      }
    },
    methods: {
        //Стилизация блоков
        blockColor(block) {
            if (block > 0) {
                if (block === 1) {
                    return 'snake'
                }
                if (block === 2) {
                    return 'food'
                } else {
                    return 'block'
                }
            }
        },
        //                                                             HELP METHODS
        randomize() {
            let y = Math.floor(Math.random() * (19 - 1) + 1)
            let x = Math.floor(Math.random() * (19 - 1) + 1)
            return [y, x]
        },
        arePointsEqual([y1, x1], [y2, x2]) {
            return y1 === y2 && x1 === x2
        },
        isPartOfSnake([y, x]) {
            let snake = this.snake
            let collisionPoints = []
            snake.forEach(point => {
                if (this.arePointsEqual([point[0], point[1]], [y, x]) === true) {
                  collisionPoints.push(1,2,3)
                } else {
                    return false
                }
            })
            if(collisionPoints.length !== 0){
               return true
            } else {
               return false
            }
        },
        //                                                                MAIN METHODS 
        calculateSnakeNextHead() {
               if (this.snakeDirection == constants.DIRECTION_RIGHT) {
                  let y = this.getSnakeHead[0]
                  let x = this.getSnakeHead[1]
                  let newX = x + 1
                  let point = [y, newX]
                  this.$store.dispatch('setNextSnakeHead', point)
                  if(this.getNewSnakeDirection === null){
                     let newDirection = this.getNewSnakeDirection
                     newDirection = this.snakeDirection
                     this.$store.dispatch('setNewSnakeDirection', newDirection)
                  }
                  this.play
               }
               if (this.snakeDirection == constants.DIRECTION_LEFT ) {
                  let y = this.getSnakeHead[0]
                  let x = this.getSnakeHead[1]
                  let newX = x - 1
                  let point = [y, newX]
                  this.$store.dispatch('setNextSnakeHead', point)
                  if(this.getNewSnakeDirection === null){
                     let newDirection = this.getNewSnakeDirection
                     newDirection = this.snakeDirection
                     this.$store.dispatch('setNewSnakeDirection', newDirection)
                  }
               }
               if (this.snakeDirection == constants.DIRECTION_UP ) {
                  let y = this.getSnakeHead[0]
                  let x = this.getSnakeHead[1]
                  let newY = y - 1
                  let point = [newY, x]
                  this.$store.dispatch('setNextSnakeHead', point)
                  if(this.getNewSnakeDirection === null){
                     let newDirection = this.getNewSnakeDirection
                     newDirection = this.snakeDirection
                     this.$store.dispatch('setNewSnakeDirection', newDirection)
                  }
               }
               if (this.snakeDirection == constants.DIRECTION_DOWN ) {
                  let y = this.getSnakeHead[0]
                  let x = this.getSnakeHead[1]
                  let newY = y + 1
                  let point = [newY, x]
                  this.$store.dispatch('setNextSnakeHead', point)
                  if(this.getNewSnakeDirection === null){
                     let newDirection = this.getNewSnakeDirection
                     newDirection = this.snakeDirection
                     this.$store.dispatch('setNewSnakeDirection', newDirection)
                  }
               }
         },
        isSnakeCrash() {
            let nextHead = this.getNextSnakeHead
            let snake = this.snake
            snake.forEach(array => {
                if (this.arePointsEqual([array[0], array[1]], [nextHead[0], nextHead[1]]) == true) {
                  this.gameClear()
                }
            });
        },
        isSnakeOutOfMap() {
            let x = this.getNextSnakeHead[1]
            let y = this.getNextSnakeHead[0]
            if (y < 0) {
               this.gameClear()
            }
            if (y > 19) {
               this.gameClear()
            }
            if (x < 0) {
               this.gameClear()
            }
            if (x > 19) {
               this.gameClear()
            }
        },
        isSnakeIfOnFood() {
            let headY = this.getNextSnakeHead[0]
            let headX = this.getNextSnakeHead[1]
            let foodY = this.getFood[0]
            let foodX = this.getFood[1]
            
            if (headX == foodX && headY == foodY) {
               return true
            } else {
               return false
            }
        },
        moveSnakeHead() {
            this.$store.dispatch('moveSnakeHead')
        },
        cutSnakeTail() {
            this.$store.dispatch('cutSnakeTail')
        },
        regenerateFood() {
            let food = this.randomize()
            if (this.isPartOfSnake(food) === true) {
               this.regenerateFood()
            } else{
               this.$store.dispatch('setFood', food)
            }
        },
        calcScore() {
         let x = this.getScore + 5
         this.$store.dispatch('calcScore', x)
        },
        calcSpeed(){
         let y = this.getScore
         if(y == 10){
            let x = this.getSpeed - 90
            let lvlName = 'GoNext'
            this.$store.dispatch('calcSpeed', x)
            this.$store.dispatch('setNameSpeed', lvlName)
         }
         if(y == 35){
            let x = this.getSpeed - 30
            let lvlName = 'notBad'
            this.$store.dispatch('calcSpeed', x)
            this.$store.dispatch('setNameSpeed', lvlName)
         }
         if(y == 55){
            let x = this.getSpeed - 20
            let lvlName = 'STOP'
            this.$store.dispatch('calcSpeed', x)
            this.$store.dispatch('setNameSpeed', lvlName)
         }
         if(y == 75){
            let x = this.getSpeed - 20
            let lvlName = 'monster'
            this.$store.dispatch('calcSpeed', x)
            this.$store.dispatch('setNameSpeed', lvlName)
         }
         if(y == 95){
            let x = this.getSpee- 20
            let lvlName = 'SOONIIC'
            this.$store.dispatch('calcSpeed', x)
            this.$store.dispatch('setNameSpeed', lvlName)
         }
        },
         loop() {
           this.calculateSnakeNextHead()
           this.isSnakeCrash()
           this.isSnakeOutOfMap()
            if(this.isSnakeIfOnFood() === true){
               this.calcSpeed()
               this.calcScore()
               this.moveSnakeHead()
               this.regenerateFood()
            } else {
              this.moveSnakeHead()
              this.cutSnakeTail()
            }
          setTimeout(() => {
             this.loop()
            }, this.getSpeed);
         },
        startGame() {
            this.$store.dispatch('clearMap')
            this.$store.dispatch('initSnake')
            this.$store.dispatch('initFood')
            setTimeout(() => {
             this.loop()
            }, 50);
         },
         gameClear() {
            this.$store.dispatch('clearMap')
            this.$store.dispatch('setGameOver', 1)
            this.$store.dispatch('snakeStop')
         }
   }
} 
</script>

